function Router() {
  //Ê†πÊçÆhashËÆæÁΩÆÈ°µÈù¢
  //ËÆæÁΩÆË∑ØÁî±
  let div
  console.log(window.location.hash)
  const number = window.location.hash.substr(1)
  console.log(number)
  //ÈªòËÆ§Ë∑ØÁî±
  if (number === "") return
  //ÂÖ∂‰ªñÊÉÖÂÜµ‰∏ãÂÖàÊ∏ÖÁ©∫È°µÈù¢
  const stage = document.querySelector(`#stage`)
  console.log(stage.children)
  Array.from(stage.children).forEach((item) => {
    console.log(item)
    if (item?.style?.display)
      item.style.display = "none"
    document.body.appendChild(item)
  })
  //Ê∑ªÂä†Ë∑ØÁî±Ë°®
  const routerHashTable = {
    "1":"#div1",
    "2":"#div2",
    "3":"#div3",
  }
  //Â§ÑÁêÜDOMÊü•ËØ¢idÂèØËÉΩÂá∫Áé∞ÁöÑÊä•Èîô
  try {
    const query = routerHashTable[number]
    div = document.querySelector(query)
    console.log(div)
  } catch {
    div = null
  }
  //Ê†πÊçÆdivÊòØÂê¶Â≠òÂú®ËøõË°åÂ±ïÁ§∫
  if (div) {
    stage.appendChild(div)
    div.style.display = "block"
  } else {
    const noMatch = document.querySelector(`#NoMatch`)
    stage.appendChild(noMatch)
    noMatch.style.display = "block"
  }
}


Router()
window.addEventListener("hashchange", Router)